<template>
    <div class="advice">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle">意见反馈</span>
            <span class="back"></span>
        </div>
        <textarea class="adviceArea" placeholder="亲爱的用户，请输入您遇到的问题以及您对哼哈太极提出的意见，以便我们提供更好的服务！"></textarea>
        <mt-button class="submitBtn" @click="submit">提交</mt-button>
    </div>
</template>

<script>
import { Toast } from 'mint-ui'
export default {
  name: 'advice',
  methods: {
    back () {
      this.$router.go(-1)
    },
    submit () {
      console.log('submit')
      Toast({
        message: '提交成功,感谢您的宝贵意见',
        duration: 2000
      })
      setTimeout(() => {
        this.$router.go(-1)
      }, 1000)
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.48rem;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .releaseIcon {
    width: 100%;
  }

  img {
    width: 100%;
  }

  .adviceArea {
    width: 8.18667rem;
    height: 5.86667rem;
    background: #f5f5f5;
    border-radius: 0.2rem;
    padding: 0.53333rem 0.50667rem;
    border: none;
    outline: none;
    resize: none;
  }

  .submitBtn {
    width: 8.77333rem;
    margin-top: 1.44rem;
    color: #f5f5f5;
    background: linear-gradient(to right bottom, #ff8309, #ffd200);
    height: 1.3333rem;
  }
</style>



